* {
  padding: 0;
  margin: 0;
  list-style-type: none;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 16px;
}
.App {
  display: flex;
  width: 1200px;
  height:400px;
  /* text-align: center; */
  margin:20px;
  border:1px solid #fff;
  border-radius: 15px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .5);
}
#map {
  width: 35vw;
  height: 55vh;
}
.location {
  /* border: 1px solid silver; */
  width: 20%;
}
.location__title {
  border-bottom: 1px solid #333;
  font-weight: bold;
  padding: 10px;
  font-size: 1.2rem;
}
.location__items {
  width: 100%;
  height: 89%;
  /* overflow: hidden; */
  overflow-y: scroll;
}
.location__loading {
  width: 100%;
  height: 89%;
  overflow-y: scroll;
}
.location__item {
  width: 99%;
  height: 50px;
  line-height: 50px;
  padding-left: 20px;
  box-sizing: border-box;
  color: rgb(53, 116, 252);
  font-size: 1.05rem;
  cursor: pointer;
  border-radius: 5px;
}
.location__item:hover {
  background: rgba(194, 194, 194, .3);
}
.location__item.active {
  color: #fff;
  background: rgb(53, 116, 252);
  border-radius: 5px;
}